import { router } from "dva";
import { dynamic } from "dva";
import userRoutes from '@routes/userRoutes';
import indexRoutes from '@routes/indexRoutes';

/**
 * <p>DvaJS+RRDv5</p>
 * @author appleyk
 * @date 20227222:52:55
 */

//  ,v5index
const allRouters = [...userRoutes, ...indexRoutes]
const { Redirect, Route, Switch, Router } = router;

/**
 * dvadynamics
 * @param {*} routes 
 * @param {*} app
 * @returns <Route/> 
 */
function mapRouteMethod(routes, app) {

    /**
     * routes
     * path : 
     * to   : 
     * exact: 
     * children : children
     * ...dynamics: () => import("@pages/xxxxx/xxxxx")
     */
    return routes.map(({ path, to, exact, children, ...dynamics }, index) => {
        if (children) {
            return (
                <Route
                    key={index}
                    path={path}
                    render={(props) => {
                        /**  */
                        const Component = dynamic({ app, ...dynamics });
                        return (
                            /** Switch */
                            <Component {...props}>
                                <Switch>
                                    {mapRouteMethod(children, app)}
                                </Switch>
                            </Component>
                        );
                    }}
                />
            );
        }

        /** path*url */
        return path !== "*" ? (
            <Route
                key={index}
                exact={exact}
                path={path}
                component={dynamic({ app, ...dynamics })}
            />
        ) : (
            <Redirect key={index} from="*" to={to} />
        );
    });
}

// 
function RouterComponent({ history, app }) {
    return (
        <Router history={history}>
            <Switch>
                {mapRouteMethod(allRouters, app)}
                {/* ,404 */}
                <Redirect to="/404" />
            </Switch>
        </Router>
    );
}

export default RouterComponent;
